<template>
<div>
    <div slot="header" class="clearfix">
        <h1 style="line-height: 36px; color: #20A0FF">豆瓣电影排行榜TOP<span>{{ articles.count }}</span></h1>
    </div>
    <el-row>
    <el-col :span="8" v-for="article in articles" :key="article.id" >
        <el-card :body-style="{ padding: '0px'}">
            <img v-bind:src="article.images.small" class="image">
            <div style="padding: 14px">
                <span>{{ article.original_title}}{{ articles.count }}</span>
                <el-rate v-model="article.rating.average" :disabled="true" :max=10 show-score></el-rate>
            </div>
            <div class="bottom clearfix">
                <time class="time">{{ article.year }}</time>
                <a type="text" class="button" v-bind:href="article.alt">Detail</a>
                <!-- <el-button type="text" class="button" v-bind:href="article.alt">Detail</el-button> -->
            </div>
        </el-card>
    </el-col>
    </el-row>
</div>
</template>

<script>
export default {
  data () {
    return {
      articles: []
    }
  },
  created: function () {
    var url = '/api' + '/v2/movie/top250?count=10'
    this.$ajax({
      url: url,
      method: 'GET'
    }).then(rsp => {
      this.articles = rsp.data.subjects
      console.log('rspData: ' + rsp)
    }).catch(function (err) {
      console.log(err)
    })
  }
}
</script>

<style>
 .time {
    font-size: 13px;
    color: #999;
    padding: 0 0 14px 14px
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0 14px;
    float: right;
  }

  .image {
    width: auto;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }
  .clearfix:after {
      clear: both
  }
</style>



