<template>
  <div class="hello" id="demo">
    <h1>Helooo</h1>
    <h1>{{ msg }}</h1>
    <myheader></myheader>
  </div>
</template>

<script>
import myheader from './myheader.vue'
export default {
  data () {
    return {
      msg: 'message'
    }
  },
  components: { myheader },
  created: function () {
    this.$ajax({
      method: 'get',
      url: 'http://localhost:9900/demo'
    }).then(rsp => {
      this.msg = rsp.data
      console.log(rsp.data)
    }).catch(function (err) {
      console.log(err)
    })
  }}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



