<template>
    <div id='myheader'>
        <span>{{ title }} @zurhan</span>
    </div>
</template>

<script>
export default {
  data () {
    return {
      title: 'China Mobile'
    }
  }
}
</script>

<style>
#myheader {
    text-align: center;
    margin: center;
    color: #272927;
    margin-top: 2px;
    font-size: 30px
}
</style>



